<template>
  <div class="bg position-r">
    <div id="mapDiv"></div>
    <!-- 地图尺寸显示开始 -->
    <span class="position-a text-center zoom">{{ zoom }}</span>
    <!-- 地图尺寸显示结束 -->
    <transition name="fade-dialog" mode="out-in">
      <div class="position-a dialog padding20" v-if="dialog">
        todo...
        <div>
          <el-button type="priamry" size="mini" @click="dialog = false"
            >关闭</el-button
          >
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapdata } from "../../utils/data";
export default {
  data() {
    return {
      map: "",
      zoom: "15",
      dialog: false,
    };
  },
  mounted() {
    this.mapinit();
  },
  methods: {
    mapinit() {
      //初始化地图对象
      let map = new T.Map("mapDiv");
      //设置显示地图的中心点和级别
      map.centerAndZoom(new T.LngLat(117.77, 39), 12);
      this.map = map;
      this.map.addEventListener("zoomend", () => {
        this.zoom = this.map.getZoom();
      });
      //允许鼠标滚轮缩放地图
      this.map.enableScrollWheelZoom();
      // 添加地图图层切换控件;
      // this.map.addControl(
      //   new T.Control.MapType({ position: T_ANCHOR_BOTTOM_RIGHT })
      // );
      //添加缩放平移控件
      this.map.addControl(
        new T.Control.Zoom({ position: T_ANCHOR_BOTTOM_RIGHT })
      );
      //创建比例尺控件对象
      let scale = new T.Control.Scale();
      //添加比例尺控件
      this.map.addControl(scale);
      this.map.disableDoubleClickZoom(); //禁用双击放大地图
      document.getElementsByClassName(
        "tdt-control-copyright tdt-control"
      )[0].style.display = "none"; //隐藏logo
      this.drawOverlay(); //绘制覆盖物
    },

    //绘制覆盖物
    drawOverlay() {
      for (let i of mapdata) {
        console.log(i);
        switch (i.type) {
          case 5:
            //多边形
            //创建面对象
            let polygon = new T.Polygon(i.latlng, i.options);
            polygon.addEventListener("click", this.clickEvent);
            //向地图上添加面
            this.map.addOverLay(polygon);
            break;
          case 4:
            //线
            //创建线对象
            let line = new T.Polyline(i.latlng, i.options);
            line.addEventListener("click", this.clickEvent);
            //向地图上添加线
            this.map.addOverLay(line);
            break;
          case 6:
            //矩形
            let bounds = new T.LngLatBounds(i.latlng[0], i.latlng[2]);
            let rect = new T.Rectangle(bounds);
            rect.addEventListener("click", this.clickEvent);
            this.map.addOverLay(rect);
            break;
          case 8:
            //圆
            let circle = new T.Circle(i.latlng, i.rY / 2, i.options);
            circle.addEventListener("click", this.clickEvent);
            //向地图上添加圆
            this.map.addOverLay(circle);
            break;
        }
      }
    },
    clickEvent() {
      this.dialog = !this.dialog;
    },
  },
};
</script>

<style lang="less" scoped>
.bg {
  padding: 0;
  height: calc(100vh - 75px);
}
#mapDiv {
  height: calc(100vh - 75px);
  width: 100%;
}
.dialog {
  height: calc(100vh - 135px);
  width: 400px;
  top: 10px;
  left: 10px;
  z-index: 9999;
  background-color: #fff;
  box-shadow: 1px 1px 5px #888;
}
.zoom {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  z-index: 999;
  bottom: 83px;
  right: 10px;
  background-color: #fff;
}

.fade-dialog-enter,
.fade-dialog-leave-to {
  opacity: 0;
  transform: translateX(-400px);
}

.fade-dialog-enter-active,
.fade-dialog-leave-active {
  transition: all 0.3s ease;
}
</style>